#navBar  {
    color: #449c9e;  
    text-decoration: none;
    font-family: "Lato", sans-serif;
    height: 50px;
    width: 100%;
    z-index: 1;
    overflow-y: hidden;
    -ms-flex-pack: end;
        justify-content: flex-end; 
    -ms-flex-align: baseline; 
        align-items: baseline;   
    padding-bottom: 2em;    
}

.small-nav div {
    padding-right: 1em;
    padding-left: 1em;
}

.topnav {      
    -webkit-transition: 0.7s;      
    -o-transition: 0.7s;      
    transition: 0.7s;
    display: -ms-flexbox;
    display: flex; 
    -ms-flex-pack: justify; 
        justify-content: space-between;
}

.small-nav {   
    margin-right: 1em;
    text-align: right;
}


@media screen and (min-width: 767px) {
    /* hides burger menu on larger screens*/
    #navBar {
        display: -ms-flexbox;
        display: flex;
        -ms-flex-pack: justify;
            justify-content: space-between;
    }
    .hamburger {
        display: none;
    }
    .small-nav {
        display: -ms-flexbox;
        display: flex;
    }
}




